import { Component, OnInit } from '@angular/core';
import { ConfirmationService, Message } from 'primeng/primeng';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';
import { BusinessCenterManageHttp } from '../http/business-center-manage.http.service';

import { TZB_COMMON_UPLOAD, TZB_COMMON_DOWNLOAD } from "app/@uisftech/common/service/http.constant";
import { ActivatedRoute } from '@angular/router';

@Component({
    selector: 'field-approve',
    templateUrl: './field-approve.component.html',
    styleUrls: ['./field-approve.component.scss'],
    providers: [Commfunc, BusinessCenterManageHttp]
})
export class FieldApproveComponent implements OnInit {
    tableDataAll: any[];
    txtDisplay: boolean;
    dtName: any;
    msgs: Message[] = [];
    resultList: any;
    dataList: any;
    dtId: any;
    doFlag: any;
    tableList: any;
    templateInfo: any;
    templateTitle: any;
    UPLOAD: any;
    DOWNLOAD: string = TZB_COMMON_DOWNLOAD;
    abledFlag: boolean;
    tableData: any = [];
    pageNum: number = 1;
    pageSize: number = 10;
    index: any;
    this_one: boolean = false;
    this_more: boolean = false;
    dimensionMaintValueList: any = [];//利率维度值
    first:number = 0;
    total:any;
    constructor(
        public routeInfo: ActivatedRoute,
        public commfunc: Commfunc,
        public httpService: BusinessCenterManageHttp,
    ) { }
    ngOnInit() {
        // this.resultList = this.routeInfo.snapshot.params;
        this.resultList = JSON.parse(this.commfunc.getSessionDataCH('yhqisp393000'));

        this.dataList = JSON.parse(this.resultList.dataMap);
        debugger
        
        console.log(this.dataList)
        if (this.dataList.fileName && this.dataList.filePath) {
            this.this_more = true;
        } else {
            this.this_one = true;
            if(this.dataList.modifyInfoMap.deleteFlag){
                this.tableDataAll = this.dataList.modifyInfoMap.deleteApprovalList;
                this.manageTableData(this.dataList.modifyInfoMap.deleteFlag);
            }else{
                this.queryDimensionMaintValueService();
            }
        }
    }

    //列表处理
    manageTableData(flag){
        if(flag == '1'){
            setTimeout(() => {
                this.total = this.tableDataAll.length;
                this.query1();
                this.tableList = this.dataList.modifyInfoMap.tableList;
            });
        }else{
            this.this_one=false;
            this.txtDisplay = true;
        }
    }
    query1() {
        let list = JSON.parse(JSON.stringify(this.tableDataAll));
        this.tableData = list.splice((this.pageNum - 1) * this.pageSize, this.pageSize);
    }
    //分页
    paginate(event) {
        //每页显示的条数
        this.pageSize = Number(event.rows);
        //当前页
        let currentPage = event.page + 1;
        this.pageNum = currentPage;
        this.first = (currentPage - 1) * this.pageSize;
        this.query1();//调用查询的方法
    }

    query() {
        this.httpService.queryDocumentTemplateDetails({ dtId: this.dataList.dtId }).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.dtId = this.dataList.dtId;
                this.dtName = data.result.templateInfo.dtName;
                this.tableList = data.result.tableList;
                this.templateInfo = data.result.templateInfo;
                this.templateTitle = this.templateInfo['dtName'];
                this.abledFlag = false;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: '查询成功' });
                this.tableData = [];
                this.tableData.push(this.dataList.modifyInfoMap);
                if (this.tableList && this.tableList.length > 0) {
                    this.tableList.forEach((element1, i) => {
                        if (element1.daField == 'preferentialAccumulationProportion') {
                            this.tableList[i].daType = '03';
                            this.tableList[i].fileList = this.dimensionMaintValueList;
                        }
                        if (this.tableList[i].fileList && this.tableList[i].fileList.length > 0) {
                            let arrTemp = [];
                            this.tableList[i].fileList.forEach((element2, ind) => {
                                let arr = { label: element2.fieldValue, value: element2.fieldKey };
                                arrTemp.push(arr);
                            });
                            this.tableList[i].fileList = arrTemp;
                        }
                    });
                }
                console.log(this.tableData);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '查询失败' });
            }
        })
    }

    //利率维度值查询
    queryDimensionMaintValueService() {
        let params = {
            dmsnlChiNm: "积数比",
            pageNum: 1,
            pageSize: 10000
        }
        this.httpService.queryDimensionMaintValueService(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                console.log(data);
                this.dimensionMaintValueList = [];
                if (data.resultList && data.resultList.legnth != 0) {
                    data.resultList.forEach(element => {
                        this.dimensionMaintValueList.push({ fieldKey: element.dmsnlValue, fieldValue: element.dmslValDesc });
                    });
                }
                this.query();
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }

    //管理查询
    manageQuery() {
        this.doFlag = '';
        let searchInfoMap = this.toParam(this.tableList, 'value');
        let param = {
            dtId: this.templateInfo['dtId'],
            entityName: this.templateInfo['entityName'],
            searchInfoMap,
            pageNum: this.pageNum,
            pageSize: this.pageSize
        }
        this.httpService.queryPermissionInfoList(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.result.permissionsInfoList) {
                    this.tableData = data.result.permissionsInfoList;
                } else {
                    this.tableData = [];
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '查询失败' })
            }
        })
    }
    toParam(list, value) {
        let param
        if (this.doFlag == '1' || this.doFlag == '2') {
            param = { seqNo: this.index };
        } else {
            param = {};
        }
        list.forEach(item => {
            if (item[value]) {
                param[item.daAnotherField] = item[value];
            }
        })
        return param;
    }
    //下载
    download() {
        window.open(this.DOWNLOAD + "?fileName=" + encodeURI(this.dataList.fileName) + "&fileUrl=" + this.dataList.filePath, "_self")
    }
}